import React, { useState } from 'react';
import clsx from 'clsx';
import './index.scss';
import noimage from '@app-assets/images/png/no-image.png';
import videodownloadbutton from '@app-assets/images/png/video-download-button.png';

function Text({
  subtitle,
  type = 'text',
  text,
  url,
  classText,
  handleClick,
  CustomComponent,
  labelColor = '#3B3E66',
}) {
  const [clicked, setClicked] = useState(false);
  const onClick = (e, url = null) => {
    if (url != null) {
      e.preventDefault();
      e.stopPropagation();
    }
    if (clicked === true) {
      return;
    }
    setClicked(true);
    if (type === 'img' && url != null) {
      window.open(url, '_blank');
    } else if (type === 'download') {
      handleClick(e);
    }
  };
  return (
    <>
      <p
        className="subtitle"
        style={{
          fontSize: '14px',
          fontWeight: 'normal',
          fontFamily: 'Heebo',
          color: labelColor,
        }}>
        {subtitle}
      </p>
      {type === 'text' && (
        <p
          className={clsx('text', classText)}
          style={{
            fontSize: '16px',
            fontWeight: 'bold',
            fontFamily: 'Heebo',
            color: '#3B3E66',
          }}>
          {text ? text : '-'}
        </p>
      )}
      {type === 'img' && (
        <a target="_blank" title="Klik untuk download" href={url}>
          <img
            className="image"
            src={url ? url : { noimage }}
            alt="foto ktp"
            onClick={onClick(url)}
          />
        </a>
      )}
      {type === 'download' && (
        <a target="_blank" title="Klik untuk download" onClick={onClick}>
          <img
            src={videodownloadbutton}
            className="image"
            alt="Klik untuk download"
          />
        </a>
      )}
      {type === 'custom' && <CustomComponent />}
    </>
  );
}

export default Text;
