import PropTypes from 'prop-types';
import MetaTags from 'react-meta-tags';
import React, { useEffect, useState, useRef } from 'react';

import {
  Row,
  Col,
  CardBody,
  Card,
  Alert,
  Container,
  Input,
  Button,
  Form,
  FormGroup,
} from 'reactstrap';
import { TextInput } from '@app-components/Text';
// Redux
import { connect, useSelector } from 'react-redux';
import { withRouter, Link } from 'react-router-dom';
// availity-reactstrap-validation
import { AvForm, AvField } from 'availity-reactstrap-validation';

// actions
import { loginUser } from '../actions';
// import images
import profile from '@app-assets/images/profile-img.png';
import logo from '@app-assets/images/logo.svg';

const Login = (props) => {
  const [valid, setValid] = useState(false);
  const [inputs, setInputs] = useState({
    username: '',
    password: '',
  });
  const { username, password } = inputs;

  const inputRefs = useRef({});

  // handleValidSubmit
  const handleValidSubmit = (event, values) => {
    event.preventDefault();
    console.log(values);
    const request = {
      password,
      username,
    };
    props.Login(request, props.history);
  };

  const handleOnEnter = (e) => {
    if (e.keyCode === 13) {
      if (inputs.username === '') inputRefs.current['username'].focus();
      else if (inputs.password === '') inputRefs.current['password'].focus();
      else if (inputs.username !== '' && inputs.password !== '') onSubmit(e);
    }
  };

  function onChange(e) {
    const { name, value } = e.target;
    setInputs((inputs) => ({
      ...inputs,
      [name]: name === 'username' ? value : value,
    }));
  }

  function onSubmit(e) {
    e.preventDefault();
    setValid(false);
    props.Login({ password, username });
  }

  useEffect(() => {
    if (username !== '' && password !== '') setValid(true);
    else setValid(false);
  }, [inputs]);
  return (
    <React.Fragment>
      <MetaTags>
        <title>Login | Whatsapp Blast</title>
      </MetaTags>
      <div className="home-btn d-none d-sm-block">
        <Link to="/" className="text-dark">
          <i className="fas fa-home h2" />
        </Link>
      </div>
      <div className="account-pages my-5 pt-sm-5">
        <Container>
          <Row className="justify-content-center">
            <Col md={8} lg={6} xl={5}>
              <Card className="overflow-hidden">
                <div className="bg-primary bg-soft">
                  <Row>
                    <Col xs={7}>
                      <div className="text-primary p-4">
                        <h5 className="text-primary">
                          Selamat datang kembali!
                        </h5>
                        <p>Masuk untuk melanjutkan</p>
                      </div>
                    </Col>
                    <Col className="col-5 align-self-end">
                      <img src={profile} alt="" className="img-fluid" />
                    </Col>
                  </Row>
                </div>
                <CardBody className="pt-0">
                  <div>
                    <Link to="/" className="auth-logo-light">
                      <div className="avatar-md profile-user-wid mb-4">
                        <span className="avatar-title rounded-circle bg-light">
                          <img
                            src={logo}
                            alt=""
                            className="rounded-circle"
                            height="34"
                          />
                        </span>
                      </div>
                    </Link>
                  </div>
                  <div className="p-2">
                    <Form className="form-horizontal">
                      <div className="mb-3">
                        <Input
                          required
                          type="text"
                          name="username"
                          value={username}
                          onChange={onChange}
                          autoFocus={true}
                          onKeyUp={handleOnEnter}
                          innerRef={(el) =>
                            (inputRefs.current['username'] = el)
                          }
                        />
                      </div>

                      <div className="mb-3">
                        <TextInput
                          name="password"
                          type="password"
                          value={password}
                          handleChange={onChange}
                          onKeyUp={handleOnEnter}
                          innerRef={(el) =>
                            (inputRefs.current['password'] = el)
                          }
                        />
                      </div>

                      <div className="mt-3 d-grid">
                        <button
                          onClick={handleValidSubmit}
                          disabled={!valid}
                          className="btn btn-primary btn-block waves-effect waves-light"
                          type="submit">
                          Masuk
                        </button>
                      </div>
                    </Form>
                  </div>
                </CardBody>
              </Card>
              <div className="mt-5 text-center">
                <p>
                  Â© {new Date().getFullYear()} Dashboard Bintang Motor{' '}
                  <i className="mdi mdi-heart text-danger" /> by IT Bintang
                </p>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};
const mapStateToProps = (state) => ({
  isLoggedIn: state.auth.isLoggedIn,
  Auth: state.auth,
});

const mapDispatchToProps = {
  Login: (payload, history) => loginUser(payload, history),
};

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Login));

Login.propTypes = {
  error: PropTypes.any,
  history: PropTypes.object,
  loginUser: PropTypes.func,
  socialLogin: PropTypes.func,
};
