import React, { useEfect, useState } from 'react';
import moment from 'moment';
import { Input, InputGroup } from 'reactstrap';
import DatePicker from 'react-datepicker';
import Select from 'react-select';
import iconEyeOpen from '@app-assets/images/svg/eye-open.svg';
import iconEyeClose from '@app-assets/images/svg/eye-close.svg';

function TextInput({
  name,
  type = 'text',
  placeholder,
  label,
  labelClassName = 'font-weight-bold pb-2 text-primary d-block',
  labelStyle = { color: '#F83245' },
  value,
  emptyValue,
  selectOption,
  handleChange,
  handleDateChange,
  handleDateTimeChange,
  startDate,
  endDate,
  minDate,
  search,
  className,
  disabled,
  isRequired,
  ...props
}) {
  const [showPassword, setShowPassword] = useState(false);
  return (
    <>
      {label && (
        <small className={labelClassName}>
          {label} {isRequired && <span style={labelStyle}> *</span>}
        </small>
      )}
      {type === 'text' && (
        <Input
          type={type}
          name={name}
          id={name}
          placeholder={placeholder}
          onChange={handleChange}
          value={value}
          disabled={disabled}
          {...props}
        />
      )}
      {type === 'date' && (
        <InputGroup>
          <DatePicker
            className="form-control"
            onChange={(date) => handleDateChange(date)}
            dateFormat="yyyy-MM-dd"
            selected={value ? moment(value).toDate() : null}
            placeholderText={placeholder}
            popperPlacement="top-end"
            startDate={startDate}
            endDate={endDate}
            minDate={minDate ? startDate : null}
            {...props}
          />
        </InputGroup>
      )}
      {type === 'datetime' && (
        <InputGroup>
          <DatePicker
            className="form-control"
            onChange={(date) => handleDateTimeChange(date)}
            dateFormat="yyyy-MM-dd hh:mm"
            selected={value ? moment(value).toDate() : null}
            showTimeSelect
            popperPlacement="top-end"
            placeholderText={placeholder}
            {...props}
          />
        </InputGroup>
      )}
      {type === 'select' && (
        <>
          {/* <Input
            type="select"
            name={name}
            id={name}
            value={value}
            onChange={handleChange}>
            <option value={emptyValue}>{placeholder}</option>
            {selectOption.map(({ name, id }) => {
              return (
                <option value={id} key={id}>
                  {name}
                </option>
              );
            })}
          </Input> */}
          <Select
            styles={{
              // Fixes the overlapping problem of the component
              menu: (provided) => ({ ...provided, zIndex: 9999 }),
            }}
            placeholder={placeholder}
            isSearchable={search}
            options={selectOption}
            value={value ? selectOption.find((e) => e.id === value) : ''}
            onChange={handleChange}
            getOptionLabel={(option) => option.name}
            getOptionValue={(option) => option.id}
            theme={(theme) => {
              return {
                ...theme,
                borderRadius: '0.29rem',
                borderWidth: 1,
                colors: {
                  ...theme.colors,
                  primary25: 'rgba(60,68,177,0.15)',
                  primary50: 'rgba(60,68,177,0.15)',
                  primary: '#3c44b1',
                },
              };
            }}
            {...props}
          />
        </>
      )}
      {type === 'password' && (
        <div style={{ position: 'relative' }}>
          <img
            src={showPassword ? iconEyeOpen : iconEyeClose}
            style={{ position: 'absolute', right: 15, top: 15 }}
            onClick={() => setShowPassword(!showPassword)}
          />
          <Input
            type={showPassword ? 'text' : 'password'}
            name={name}
            id={name}
            placeholder={placeholder}
            onChange={handleChange}
            value={value}
            disabled={disabled}
            style={{ paddingRight: 35 }}
            {...props}
          />
        </div>
      )}
    </>
  );
}

export default TextInput;
